import '../../styles/css/quisuisje.css'
import CV from '../../assets/CVYanguelCornillet.pdf'
import PhotoCv from '../../assets/CVYanguelCornillet.png'
import { useRef } from 'react'
import useSlideInOut from '../slideRightIn'

function QuiSuisJe() {
  const elementRef = useRef(null)
  useSlideInOut(elementRef)
  return (
    <section id="quiSuisJe">
      <div className="containerQuiSuisJe">
        {/* Li  l'animation slide */}
        <div ref={elementRef} className="slide-in-out titleQuiSuisJe">
          <h1> Qui suis-je ?</h1>
        </div>
        {/* --------------------*/}
        <p className="pQuiSuisJe">
          Je mappelle <strong>Yangul Cornillet</strong>, je suis{' '}
          <strong>dveloppeur Web junior </strong> et j'habite dans le
          Maine-et-Loire (49). <br />
          J'ai effectu une reconversion professionnelle dans le{' '}
          <strong>Dveloppement Web</strong> chez OpenClassrooms pour consolider
          mes connaissances et devenir un dveloppeur web fullstack, domaine qui
          mintresse depuis de nombreuses annes. <br />
          Mes annes d'expriences dans le commerce m'ont permis dacqurir des
          comptences importantes telles que l
          <strong>organisation</strong>, la
          <strong> ponctualit</strong> ainsi que des qualits relationnelles
          utile lors d'un projet en groupe comme l'
          <strong>coute</strong> ou bien la <strong>remise en question</strong>
          . <br />
          Je suis <strong>ambitieux et enthousiaste</strong>  l'ide de mettre
          mes comptences et mon savoir-faire au service de projets
          passionnants. <br />
          Je vous invite  dcouvrir mes projets, qui vous donneront un aperu
          de mon parcours et de mes comptences.
        </p>
        <a href={CV} download>
          <button className="btnCV"> Tlcharger mon CV </button>
        </a>
      </div>
      <div className="CV">
        <img src={PhotoCv} alt=" Visuel du CV " className="photoCv"></img>
      </div>
      <div className=" iconeFlecheQuiSuisJe slide-bottom">
        <i className="fa-solid fa-circle-arrow-down fa-xl arrow"></i>
      </div>
    </section>
  )
}

export default QuiSuisJe
